<template lang="en">
<section>
    <div class="modal my-popups right fade show" role="dialog" style="display: block;">
        <div class="modal-dialog mw-1100" role="document">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" @click="closepopup"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 class="modal-title" id="myModalLabel">{{ $t('ManageCampaign') }}</h4>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-SendType="{}">
                            <ul id="ManageCampaignTypes" class="shortcuts-menu pb-3">
                                <li class="position-relative"><a class="short-menu-item" style="background: rgba(0, 141, 224, 0.275);" @click="onClickSendOrSchedule('SendNow')">
                                    <span class="left-icon-m m-3 text-white" style="background: rgba(0, 141, 224, 0.79);"> <i class="fa fa-envelope"></i></span>
                                    <span class="short-item-name">Send Now</span></a>
                                </li>
                                <li class="position-relative"><a class="short-menu-item" style="background: rgba(24, 0, 224, 0.275);" @click="onClickSendOrSchedule('Schedule')">
                                    <span class="left-icon-m m-3 text-white" style="background: rgba(24, 0, 224, 0.44);"> <i class="fa fa-share-alt"></i></span>
                                    <span class="short-item-name">Schedule</span></a>
                                </li>
                                <li class="position-relative"><a class="short-menu-item" style="background: rgba(229, 163, 14, 0.275);" @click="onClickSendOrSchedule('BatchSchedule')">
                                    <span class="left-icon-m m-3 text-white" style="background: rgba(229, 163, 14, 0.75);"><i class="fa fa-phone"></i></span>
                                    <span class="short-item-name">Batch Schedule</span></a>
                                </li>
                            </ul>
                        </template>
                        <template v-slot:tgslot-Schedule="{data}"> 
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0" >
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2 float-left px-1">
                                        <div class="form-group">
                                            <label>Schedule At:</label>
                                            <div>
                                                <div class="col-5 col-sm-5 col-md-5 col-lg-5 col-xl-5 float-left px-0">
                                                    <div class="input-group">
                                                        <v-date-picker :popover="popover" :min-date="getMinDate()" v-model="ScheduleAtDate"
                                                            :minute-increment="5" class="input-group" >
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append">
                                                                    <span class="input-group-text">
                                                                        <em class="fa fa-calendar"></em>
                                                                    </span>
                                                                </div>
                                                            </template>
                                                        </v-date-picker>
                                                     </div>
                                                </div>
                                                <div class="col-5 col-sm-5 col-md-5 col-lg-5 col-xl-5 float-left pr-0">
                                                    <div class="input-group">
                                                        <v-date-picker :popover="popover" :min-date="getMinDate()" v-model="ScheduleAtTime"
                                                        :minute-increment="5" class="timedate timedate_hs input-group"  mode="time">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input placeholder="HH:MM" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>
                                                    </div>
                                                </div>
                                                <div class="col-9 col-sm-10 col-md-10 col-lg-10 col-xl-10 float-left mt-2 px-0">
                                                    <select  v-bind:class="{'form-control error_bodred':true}"  data-vv-as="Time Zone" v-model="ScheduleAtTimeZone" >
                                                        <option value="">Select Time Zone</option>
                                                        <option v-for="zone in timezone" :title="zone.name" :key="zone" v-bind:value="zone.value">
                                                            {{TruncateStr(zone.name,50)}}
                                                        </option>
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        </template>
                         <template v-slot:tgslot-Batch="{data}">
                             <div class="form-group">
                                <div class="row justify-content-between align-items-center flex-nowrap">
                                    <div class="col-2 col-sm-2 col-md-2 col-lg-2 col-xl-2 float-left">
                                        <div class="form-group">
                                            <label>Start On(M/D/YYYY):</label>
                                            <v-date-picker :popover="popover" :min-date="getMinDate()"  :minute-increment="5" class="input-group" v-model="BatchStartDate">
                                                <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                    <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                    <div @click="togglePopover()" class="input-group-append">
                                                        <span class="input-group-text">
                                                            <em class="fa fa-calendar"></em>
                                                        </span>
                                                    </div>
                                                </template>
                                            </v-date-picker>
                                        </div>
                                    </div>
                                    <div class="form-group col-3">
                                        <label>Batch Quantity</label>
                                        <div class="d-flex">
                                            <div class="input-group mr-1">
                                                <input class="form-control" v-model="BatchQuantity" type="number" name="batchQuantity"/>
                                            </div>
                                            <div class="input-group">
                                                <select v-model="BatchType" id="time" class="form-control">
                                                    <option value="">Select</option>
                                                    <option value="Days">Days</option>
                                                    <option value="Hours">Hours</option>
                                                    <option value="Minutes">Minutes</option>
                                                    <option value="Seconds">Seconds</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-group col-2">
                                        <label>Repeat After:</label>
                                        <div class="input-group">
                                            <input class="form-control" v-model="RepeatAfter" type="number" name="repeatAfter"/>
                                        </div>
                                    </div>
                                    <div class="form-group col-2">
                                        <label>Select Day:</label>
                                        <div class="input-group">
                                            <select v-model="BatchDays" class="form-control" id="day">
                                                <option value="">Select Day</option>
                                                <option value="1">Monday</option>
                                                <option value="2">Tuesday</option>
                                                <option value="3">Wednesday</option>
                                                <option value="4">Thursday</option>
                                                <option value="5">Friday</option>
                                                <option value="6">Saturday</option>
                                                <option value="7">Sunday</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="form-group col-3">
                                        <label>Specify start and end time:</label>
                                        <div class="d-flex no-wrap">
                                            <div class="input-group mr-2">
                                                <v-date-picker :popover="popover" :min-date="getMinDate()" :minute-increment="5" class="timedate timedate_hs input-group"  mode="time" v-model="BatchStartTime">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input placeholder="Starts At" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                            <div class="input-group">
                                                <v-date-picker :popover="popover" :min-date="getMinDate()"  :minute-increment="5" class="timedate timedate_hs input-group"  mode="time" v-model="BatchEndTime">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input placeholder="Ends At" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>
                    </dynamic-form>
                </div>
            </div>
        </div>
    </div>
     <modaladdsmtp v-if="isShowSMTPModal" :SendThrough="SendThrough" :ProfileId="ProfileId"  @close="isShowSMTPModal=false"></modaladdsmtp>
</section>
</template>

<script>
import CampaignDataServices from "../../../services/DataService";
import modaladdsmtp from './modaladdsmtp.vue';
export default {
    components: {modaladdsmtp},
    props: { 
        campaignId:  {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            Contactlist:[],
            timezone:[],
            isShowSMTPModal:false,
            Grouplist:[],
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        name: "SendType",
                        value: "",
                        placeholder: "",
                    }, ]
                },
                {
                    layoutType: "double",
                    Data: [{astype: "SlotField",
                        name: "Schedule",
                        value: "",
                        placeholder: "", 
                        visibility: false,
                    },]
                },
                {
                    layoutType: "single",
                    Data: [{astype: "SlotField",
                        name: "Batch",
                        value: "",
                        placeholder: "", 
                        visibility: false,
                    },]
                },
                {
                    layoutType: "triple",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('SendThrough'),
                            name: "SendThrough",
                            value: "",
                            placeholder: "Select Send Through",
                            config: {
                                options: [{name:'MailChimp',value:'MailChimp'},{name:'SendGrid',value:'SendGrid'},{name:'SMTP',value:'SMTP'}],
                                onChange: this.onChangeSendThrough
                            },
                            validationRules: {"required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Profile'),
                            name: "Profile",
                            value: "",
                            placeholder: "Profile",
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                                showAddIcon: true,
                                onAddButtonClick: this.AddSMTPCredentials,
                            },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('TrackEmail'),
                            name: "TrackEmail",
                            value: "",
                            config: {
                                options: [{ name: '', value: true }]
                            },
                            validationRules: { "required": false },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "TextField",
                            label: this.$t("FromEmail"),
                            name: "FromEmail",
                            value: "",
                            placeholder: "",
                            config: {},
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Recipient'),
                            name: "Recipient",
                            value: "1",
                            placeholder: "",
                            config: {
                                options: [{value: "1",name: "Choose Contact"}, {value: "2", name: "Send to Smart List"}],
                                onChange: this.onclickContactorGroup
                            },
                            visibility: true,
                            validationRules: { "required": false},
                        },
                        {
                            astype: "TextField",
                            label: this.$t("FromName"),
                            name: "FromName",
                            value: "",
                            placeholder: "",
                            config: {},
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('Contact'),
                            name: "Contact",
                            value: "",
                            mode: "tag",
                            config: {
                                options: []
                            },
                            visibility:true,
                            validationRules: {"required": true },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('ContactGroup'),
                            name: "ContactGroup",
                            value: "",
                            mode: "tag",
                            config: {
                                options: []
                            },
                            visibility:false,
                            validationRules: {"required": true },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [{
                        astype: "TextField",
                        label: this.$t("Subjectline"),
                        name: "Subjectline",
                        value: "",
                        placeholder: "",
                        config: {},
                    }, ]
                },
                {
                    layoutType: "double",
                    Data: [{
                        astype: "TextField",
                        label: this.$t("PreviewText"),
                        name: "PreviewText",
                        value: "",
                        placeholder: "",
                        config: {},
                    }, ]
                }
                
            ],
            buttons: [{
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    //onClick: this.ClearAll
                },
                {
                    type: "submit",
                    class: "btn btn-primary",
                    text: "<i class='fa fa-envelope pr-2'></i>Schedule",
                    //onClick: this.ClearAll
                },

            ],
            SendThrough:'',
            ProfileId:0,
            SendType:'',
            ScheduleAtDate:null,
            ScheduleAtTime:null,
            ScheduleAtTime:null,
            BatchStartDate:null,
            BatchQuantity:null,
            BatchType:null,
            RepeatAfter:null,
            BatchDays:null,
            BatchStartTime:null,
            BatchEndTime:null
            
        }
    },
    created: function () {
        var vm=this;
        vm.GetContacts();
        vm.GetTimeZoneList();
    },
    methods: {
        closepopup() {
            var vm = this;
            vm.$parent.closeManageCampaign();
        },
        GetContacts:  function () {
            var vm = this;
            var url = `id=&moduleName=CRM&type=CRM_CONTACTS&search=&refId=`;
            CampaignDataServices.getDDLData(url).then(function (response) {
                vm.Contactlist=[];
                vm.FormSchema[4].Data[3].config.options=[];
                vm.Contactlist = response.data.DATA;
                vm.Contactlist.forEach((item, index) =>{
                vm.FormSchema[4].Data[3].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
            });
        },
        onChangeSendThrough(event){
            var vm =this;
            vm.ProfileId=0;
            vm.SendThrough=event.target.value;
            vm.FormSchema[3].Data[1].value='';
            vm.FillProfileDropdown(vm.SendThrough);
        },
        FillProfileDropdown(type){
            var vm =this;
            vm.FormSchema[3].Data[1].config.options=[];
            var url = `id=&moduleName=CRM&type=${type}`;
            CampaignDataServices.getDDLData(url+ "&search=").then(response => {
                if(response.data.DATA != undefined){
                    var options = response.data.DATA;
                    options.forEach(item=>{
                    vm.FormSchema[3].Data[1].config.options.push({name:item.name, value:item.value})
                })
                }
            });
        },
        onChangeProfile(event){
            var vm=this;
            vm.ProfileId=event.target.value;
        },
        AddSMTPCredentials(){
            var vm =this;
            if(vm.FormSchema[3].Data[0].value==''){
                vm.ShowAlert('Please select atleast one value in send through dropdown.', "warning", true, vm.$t("Alert"));
                return false;
            }
            vm.isShowSMTPModal=true;
        },
        onClickSendOrSchedule(type){
            var vm = this;
            vm.ResetProperties();
            vm.SendType=type;
            if(type=='SendNow'){
                vm.FormSchema[1].Data[0].visibility=false;
                vm.FormSchema[2].Data[0].visibility=false;
            }else if(type=='Schedule'){
                vm.FormSchema[1].Data[0].visibility=true;
                vm.FormSchema[2].Data[0].visibility=false;
            }else{
                vm.FormSchema[1].Data[0].visibility=false;
                vm.FormSchema[2].Data[0].visibility=true;
            }
        },
        GetTimeZoneList: function () {
            var vm = this;
           var url = "id=&moduleName=CRM&type=TIMEZONE&search=&refId="
           CampaignDataServices.getDDLData(url).then(function (response) {
                vm.timezone = response.data.DATA;
            });
        },
        GetContactGroup: function (){
            var vm = this;
            var url = 'id=&subModuleCode=CRM_CAMPAIGNS';
            CampaignDataServices.ContactGroupListing(url).then(function (response){
                vm.Contactlist=[];
                vm.FormSchema[4].Data[3].config.options=[];
                vm.Contactlist = response.data.data;
                vm.Contactlist.forEach((item, index) =>{
                vm.FormSchema[4].Data[4].config.options.push({
                        name: item.NAME,
                        value: item.GROUP_ID
                    })
                })
            }) 
        },
        onclickContactorGroup: function(event){
            var vm = this;
            var value = event.target.getAttribute('data-value');
            vm.FormSchema[4].Data[4].value='';
            vm.FormSchema[4].Data[3].value='';
            if(value=='1'){
                vm.GetContacts();
                vm.FormSchema[4].Data[4].visibility=false;
                vm.FormSchema[4].Data[3].visibility=true;
            }
            else{
                vm.GetContactGroup();
                vm.FormSchema[4].Data[3].visibility=false;
                vm.FormSchema[4].Data[4].visibility=true;
            }
        },
        ResetProperties(){
            var vm=this;
            vm.BatchStartDate=null;
            vm.BatchQuantity=null;
            vm.BatchType='';
            vm.RepeatAfter=null;
            vm.BatchDays='';
            vm.BatchStartTime=null;
            vm.BatchEndTime=null;
            vm.ScheduleAtDate=null;
            vm.ScheduleAtTime=null;
            vm.ScheduleAtTimeZone='';
        },
        onSubmit: function (formFeilds, values) {
            var vm = this;

            var schedule ={
                date    : vm.ScheduleAtDate,
                time    : vm.ScheduleAtTime,
                timezone: vm.ScheduleAtTimeZone
            }

            var batch_schedule ={
                start_date      : vm.BatchStartDate,
                batch_quantity  : vm.BatchQuantity,
                batch_type      : vm.BatchType,
                repeat_after    : vm.RepeatAfter,
                batch_days      : vm.BatchDays,
                start_time      : vm.BatchStartTime,
                end_time        : vm.BatchEndTime
            }

            var jsonObj ={
                campaign_id     : vm.campaignId,
                sub_module_code : 'CRM_CAMPAIGNS',
                send_type       : vm.SendType,
                send_through    : formFeilds.SendThrough,
                profile_id      : formFeilds.Profile,
                track_email     : formFeilds.TrackEmail=='' ? false : true,
                recipient       : formFeilds.Recipient,
                from_email      : formFeilds.FromEmail,
                from_name       : formFeilds.FromName,
                contact         : formFeilds.Contact,
                contact_group   : formFeilds.ContactGroup,
                subject         : formFeilds.Subjectline,
                preview_text    : formFeilds.PreviewText,
                schedule        : schedule,
                batch_schedule  : batch_schedule
            }

            var url = JSON.stringify(jsonObj);
            CampaignDataServices.SaveScheduleCampaign(url).then(response => {
                if(response.data=='SENT'){
                    vm.ShowAlert("Email Sent Successfully.","success", true, vm.$t("Alert"));
                }else if(response.data=='SCHEDULED'){
                    vm.ShowAlert("Email Scheduled Successfully.","success", true, vm.$t("Alert"));
                }else if(response.data=='BATCH_SCHEDULED'){
                    vm.ShowAlert("Email Batch Scheduled Successfully.","success", true, vm.$t("Alert"));
                }else{
                    vm.ShowAlert("Something went wrong.","danger", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>
<style scoped>
.shortcuts-menu li {
    width: 100%;
    max-width: 33%;
    padding: 15px;
    float: left;
    display: flex;
    transition: all .4s;
}
</style>
