<template>
    <section>
        <div class="modal my-popups right fade show" role="dialog" style="display: block;">
            <loader :is-visible="isLoading"></loader>
            <div class="modal-dialog mw-1100" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="closepopup"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                        <h4 id="myModalLabel" class="modal-title">{{ $t("ManageCampaign") }}</h4>
                    </div>
                    <div class="border p-3">
                        <dynamic-form :ref="form" :schema="FormSchema" :buttons="buttons" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-CampaignType="{ data }">
                                <ul id="divChannelTypes" class="shortcuts-menu">
                                    <li class="position-relative"><a class="short-menu-item"
                                            @click="onClickCampaignType(1)"
                                            style="background: rgba(0, 141, 224, 0.275);"><span
                                                class="left-icon-m m-3 text-white"
                                                style="background: rgba(0, 141, 224, 0.79);">
                                                <i class="fa fa-envelope"></i></span><span class="short-item-name">Email
                                                Campaign</span></a></li>
                                    <li class="position-relative"><a class="short-menu-item"
                                            @click="onClickCampaignType(2)"
                                            style="background: rgba(24, 0, 224, 0.275);"><span
                                                class="left-icon-m m-3 text-white"
                                                style="background: rgba(24, 0, 224, 0.44);"><i
                                                    class="fa fa-share-alt"></i></span><span class="short-item-name">Social
                                                Media Campaign</span></a></li>
                                    <li class="position-relative"><a class="short-menu-item"
                                            @click="onClickCampaignType(3)"
                                            style="background: rgba(229, 163, 14, 0.275);"><span
                                                class="left-icon-m m-3 text-white"
                                                style="background: rgba(229, 163, 14, 0.75);"><i
                                                    class="fa fa-phone"></i></span><span class="short-item-name">SMS
                                                Campaign</span></a></li>
                                </ul>
                            </template>
                            <template v-slot:tgslot-SelectTemplate="{ data }">
                                <div class="row">
                                    <div class="col-2 float-right">
                                        <input name="search" v-model="searchTemplate" placeholder="Search Template"
                                            type="text" class="form-control" @keyup="onSearch">
                                    </div>
                                </div>
                                <div class="row mt-2">
                                    <div class="card ml-3">
                                        <div class="card-body">
                                            <h5 class="card-title"></h5>
                                            <a @click="createTemplate()">
                                                <h6 class="card-subtitle">Create Template</h6>
                                            </a>
                                        </div>
                                    </div>
                                    <div class="ml-2 mt-2 card" v-for="(item, index) in displayedTemplates" :key="index">
                                        <div class="card-body">
                                            <!-- <span v-html="item.TEMPLATE_CONTENT"></span> -->
                                            <a @click="Templateclick(item.TEMPLATE_ID)">
                                                <h5 class="card-title">{{ item.TEMPLATE_NAME }}</h5>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                                <button class="mt-2 float-right" v-if="!showAll" @click="showAll = true">View All</button>
                            </template>
                        </dynamic-form>
                    </div>
                </div>

            </div>
        </div>

    </section>
</template>
<script>
import DataServices from "../../../services/DataService";
export default {
    props: { 
        campaignId:  {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            StaticObj:
            {
                layoutType: "Single",
                Data: [
                    {
                        astype: "SlotField",
                        label: "",
                        name: "CampaignType",
                        value: "",
                        placeholder: "",
                    },
                ],
            },
            StaticObj2:
            {
                layoutType: "Single",
                group_name: 'SelectTemplate',
                group_display_name: this.$t('SelectTemplate'),
                Data: [{
                    astype: "SlotField",
                    name: "SelectTemplate",
                    value: "",
                    placeholder: "",
                },]
            },
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    value: "Save",
                    //onSubmit: this.onSubmit


                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closepopup,
                    isVisible: true,

                },

            ],
            FormSchema: [],
            campaignOwnerDDL: [],
            EmailTemplateData: [],
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            showAll: false,
            search: '',
            searchTemplate: '',
            campaignType: 0,
            CampaignData: [],
            GroupBy: [],
            isLoading: false,
            CampaignId: 0,
            CampaignOwnerData: [],
            templateId: 0,
            statusId: 1001,
            CampOwner:''

        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
        await vm.GetDropDownData();
        await vm.emailTemplateData();
    },
    computed: {
        displayedTemplates() {
            return this.showAll ? this.EmailTemplateData : this.EmailTemplateData.slice(0, 4);
        },
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            const params = `id=${vm.campaignId}&moduleName=CRM&subModuleCode=CRM_CAMPAIGNS&langCode=en`;
            await DataServices.getFormLayout(params).then(async function (response) {
                vm.isLoading = false;
                vm.CampaignData = response.data.data;
                if (vm.CampaignData != null && vm.CampaignData.length > 0) {
                    var groupId = vm.CampaignData[0].group_id;
                    vm.GroupBy.push(vm.StaticObj);
                    $.each(vm.CampaignData, $.proxy(async function (index, item) {
                        if (item.form_field_visibility == 'NO') {
                            return;
                        }
                        groupId = item.group_id;
                        if (item.dt_code == 'date') {
                            item.value = item.value == '' ? null : item.value;
                        }
                        if (item.dt_code == 'checkbox') {
                            item.select_value = item.value.split(',');
                        }
                        var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vm.CampaignData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            // if (vm.CampaignId > 0) {
                            //     $.each(returnedData, function (filedIndex, fieldItem) {
                            //         if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                            //             fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                            //             fieldItem.select_value = fieldItem.select_options;
                            //             if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                            //                 fieldItem.value = fieldItem.select_options[0];
                            //             }
                            //         }
                            //         else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES" && fieldItem.value > 0) {
                            //             fieldItem.value = parseFloat(fieldItem.value, 10).toFixed(2);
                            //         }
                            //     });
                            // }

                            var finalData = [];
                            returnedData.forEach(item => {
                                if (item.form_field_visibility == 'YES') {
                                    let DataObj = {};
                                    if (item.astype == 'TextField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'NumericField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'CustomSelectField') {
                                        vm.CampOwner=item.value;
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            config: {
                                                options: []
                                            },
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'SelectField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            config: {
                                                options: []
                                                //options: JSON.parse(item.select_options)
                                            },
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'TextAreaField') {
                                        DataObj = {
                                            astype: item.astype,
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }
                                    if (item.astype == 'DatetimeField') {
                                        DataObj = {
                                            astype: 'DateField',
                                            label: item.display_name,
                                            name: item.name,
                                            value: item.value,
                                            disabled: false,
                                            validationRules: { "required": item.system_is_required },
                                        }
                                        finalData.push(DataObj);
                                    }

                                }
                            })

                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    layoutType: "double",
                                    group_name: returnedData[0].group_name,
                                    group_display_name: returnedData[0].group_display_name,
                                    Data: finalData
                                };
                                vm.GroupBy.push(objGroup);
                            }
                        }
                    }))
                    vm.GroupBy.push(vm.StaticObj2);
                    vm.FormSchema = vm.GroupBy;

                    // if (vm.CampaignId > 0) {
                    //     var frmFields = {}
                    //     vm.CampaignData.forEach((item) => {
                    //         if (item.astype != '' && item.form_field_visibility == 'YES') {
                    //             if (item.name == 'campaign_owner' && typeof item.value === 'object' && item.value !== null) {
                    //                 frmFields[item.name] = item.value.value;
                    //             } else {
                    //                 frmFields[item.name] = item.value;
                    //             }
                    //         }
                    //     })
                    //    // vm.$refs.form.resetForm({ values: frmFields });
                    // }
                    // setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
                    // setTimeout(function () { vm.RadioButtonBootstrap() }, 500);
                }
            });
        },
        async GetDropDownData() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_USERS";
            await DataServices.getDDLData(url + "&search=").then(response => {
                var json = [];
                if (response.data == '') {
                }
                if (response.data != '') {
                    json = response.data;
                }
                var campDDLData = [];
                vm.CampaignOwnerData = json.DATA;
                if (vm.CampaignOwnerData != null) {
                    vm.CampaignOwnerData.forEach(x => {
                        campDDLData.push({
                            name: x.name,
                            value: x.value
                        })
                    })
                    vm.CampaignOwnerData = campDDLData;

                    vm.GroupBy.forEach(x => {
                        if (x.group_name == 'campaign_details') {
                            // x.Data.filter(x=>x.name=='campaign_owner')[0].astype='MultiSelectField';
                            // x.Data.filter(x=>x.name=='campaign_owner')[0].mode='tag';
                            x.Data.filter(x => x.name == 'campaign_owner')[0].config.options = vm.CampaignOwnerData;
                        }
                    })
                    var cObj=vm.CampaignOwnerData.filter(x=>x.value==vm.CampOwner);               
                    var CampObjChild=[];
                    cObj.forEach(item=>{
                        CampObjChild.push({
                            label:item.name,
                            value:item.value
                            })
                        })
                    vm.FormSchema[1].Data[1].value=CampObjChild;
                }
            });
        },
        closepopup() {
            var vm = this;
            vm.$parent.CloseManageCampaignPopup();
        },
        async emailTemplateData() {
            var vm = this;
            var url = 'id=0&search=' + vm.search + '&refId=' + 477 + '&subModuleCode=CRM_CAMPAIGNS&sortBy=' + vm.SortBy + '&sortExp=' + vm.SortExp + '&pageSize=' + vm.PageSize + '&pageNumber=' + vm.PageNumber;
            DataServices.TemplateListing(url).then(function (response) {
                if (response.data == null || response.data == "") {
                    vm.NoRecordForModel = true;
                }
                else {
                    vm.EmailTemplateData = response.data.data;
                }
            });
        },
        onSubmit(formfields, val) {
            var VueObj = this;
            VueObj.isLoading = true;
            var data = [];
            var fullJson = VueObj.CampaignData;
            $.each(fullJson, function (key, item) {

                if (item.name == 'campaign_name') {
                    item.value = formfields.campaign_name;
                }
                else if (item.name == 'campaign_owner') {
                    item.value = formfields.campaign_owner;
                }
                else if (item.name == 'campaign_description') {
                    item.value = formfields.campaign_description;
                }
                else if (item.name == 'campaign_type') {
                    item.value = VueObj.campaignType;
                }
                else if (item.name == 'status_id') {
                    item.value = VueObj.statusId;
                } else if (item.name == 'template_id') {
                    item.value = VueObj.templateId.toString();
                }
                var fieldValue = (item.name == "campaign_id" ? VueObj.campaignId : item.value);
                if (item.dt_code == "date") {
                    fieldValue = item.value;
                }
                if (item.dt_code == "checkbox") {
                    var filtered = item.select_value.filter(function (el) {
                        return (el != null && el != "");
                    });
                    fieldValue = filtered.join(',');
                }
                if (item.dt_code == "select") {
                    try {
                        fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                    } catch (ex) { }
                }
                var dataObject = {
                    field_name: item.name,
                    field_value: fieldValue,
                    custom_field_id: item.custom_field_id
                };
                data.push(dataObject);
            });
            var postJSON = {
                data: data,
            };
            var objectPostString = JSON.stringify(postJSON);
            DataServices.PostCampaign(objectPostString).then(function (response) {
                VueObj.isLoading = false;
                if (response.data[0].Code == "1") {
                    if (VueObj.campaignId > 0) {
                        VueObj.ShowAlert(VueObj.$t('UpdatedSuccess', [VueObj.$t('Campaign')]), "success", true, VueObj.$t("Alert"));
                         VueObj.$parent.FetchData();
                         VueObj.closepopup(); 
                    }
                    else {
                        VueObj.ShowAlert(VueObj.$t('AddedSuccess', [VueObj.$t('Campaign')]), "success", true, VueObj.$t("Alert"));
                        VueObj.$parent.FetchData();
                        VueObj.closepopup(); 
                    }
                    VueObj.$router.push({ name: 'CRMCampaign' });
                }
                else {
                    VueObj.ShowAlert(VueObj.$t('AddedError', [VueObj.$t('Campaign')]), "failure", true, VueObj.$t("Alert"));
                    VueObj.$parent.FetchData();
                    VueObj.closepopup(); 
                }
            });
           
        },
        async onClickCampaignType(type) {
            var vm = this;
            vm.campaignType = type;
        },
        async onSearch() {
            var vm = this
            vm.search = vm.searchTemplate;
            vm.emailTemplateData();
        },
        async Templateclick(item) {
            var vm = this;
            vm.templateId = item;

        },
        async createTemplate() {
            window.open('https://localhost:44346/emailtemplate/addnew?reqFrom=ManageCampaign', '_blank');
        }
    }
}
</script>
<style>
.shortcuts-menu li {
    width: 100%;
    max-width: 33%;
    padding: 15px;
    float: left;
    display: flex;
    transition: all .4s;
}</style>